import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { BehaviorSubject } from 'rxjs';
export const API_URL = environment.BASE_URL;

@Injectable({
  providedIn: 'root',
})
export class VideoService {
  constructor(private httpClient: HttpClient) {}

  fetchCustomerDetails(payload: any) {
    return this.httpClient.post<any>(
      `${API_URL}/customerdata/phoneNoEmailIdExists`,
      payload
    );
  }

  upsertCustomer(data: object) {
    return this.httpClient.post<any>(
      `${API_URL}/customerdata/upsertCustomerDetails`,
      data
    );
  }

  updateKyc(kycDetails: any) {
    return this.httpClient
      .post<any>(`${API_URL}/customerdata/saveRekycOrAcceptInfo`, kycDetails)
  }

  saveCustBasicInfoData(icustCustVideoVerifyDto) {
    return this.httpClient.post<any>(
      `${API_URL}/icust-video-verify/save`,
      icustCustVideoVerifyDto
    );
  }
}
